var util = require('util');
var security = require("security");
var kit = require("kit");

var moduleConfig = {
	configCodes:[],
	rowNum:10,
	stationCode:"",
	title:"",
	reverse:"",// 是否需要反转
};

var productExceptionCloseCtrl = {

	page : undefined,
	init: function() {
	
		security.getSysModuleConfig("PCS", "ERROR_CONFIRM", (config) => {
			moduleConfig = config;
			this.event();
		});
	},
	event: function() {
		util.enterEvent(() => {
			this.confrim(); 
		});

		$("#confirm").click((event) => {
			this.confrim();
		});

		util.fliterInput("#productNum");
	},
	confrim: function() {

		if (!$("#productNum").val()) {
			return;
		}

		util.services(
		{
			serviceCode: "GMES-CLIENT-PCS00019", 
			data: {
				stationCode: moduleConfig.stationCode,
				exceptionCode:$("input[name=error-type]:checked").val(),
				productNum: $("#productNum").val()
			},
			message: "正在确认"
		}
		).done(function(response) {
			util.rightCode(response,(response) => {
				notie.alert("确认成功！");
			});

			$("#productNum").focus().select();
		})
		.fail(function() {
			$("#productNum").focus().select();
		});
	},
	getItem: function() {
		util.service("GMES-CLIENT-PCS00020", {
			stationCode: moduleConfig.stationCode,
		},"正在获取异常列表")
			.done(function(response) {

				util.rightCode(response,(response) => {
					kit.tempRender($("#featureTemp").html(),$("#featureRender"), {list: response.data});
				});
			});
	}
};

module.exports = productExceptionCloseCtrl;



